import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class PosterInfo extends Component {
    static defaultProps = {
        info : {
            pName : "test",
            pDate : "test11",
            id : 0
        }
    }

    openReservStep = () => {
        window.open("/ReactApp_K/#/reservStep/" + this.props.info.id ,"_blank","width=1200,height=800,toolbar=no,resizable=no");
    }

    render() {
        const { info } = this.props;
        const style = {
            display: "block"
        }
        return (
            <div className="swiper-slide">
                <a href={process.env.PUBLIC_URL + "/#/detail/" + info.id} className="k-link__wrap"  style={style}>
                    <div className="k-poster__wrap" data-num={info.pNum}>
                        <mark className="k-top__label">{info.pTopLabel}</mark>
                        <img className="k-poster__img" src={require('../images/posters/' + info.pImg)} alt={info.pName + " 포스터"}/>
                    </div>

                    <div className="poster-txt__wrap">
                        <div className="poster-label__wrap">
                            {info.pLabel.map(function(name, index){
                                return <mark className="poster-label" key={"poster-label"+index}>{name.title}</mark>;
                            })}
                        </div>
                        <h3 className="poster-title">{info.pName}</h3>
                        <p className="poster-date">{info.pDate}</p>
                        <p className="poster-place">{info.pPlace}</p>
                    </div>
                </a>
                <div className="poster-btn__wrap">
                    <Link to={"/detail/" + info.id} className="poster-btn detail-btn">상품상세</Link>
                    <a href="javascript:void(0)" className={"poster-btn right-btn " + ( info.reservActive ? "pre-reserv" : "end-reserv" )} onClick={(info.reservActive ? this.openReservStep : "")}>{info.reservActive ? "예매하기" : "예매마감"}</a>
                </div>
            </div>
        );
    }
}

export default PosterInfo;