import React, { Component } from 'react';

class Promotion extends Component {
    static defaultProps = {
        data: []
    }
    render() {
        const { data } = this.props;
        const list = data.map((info,index) => (
                <li key={"promotion"+index}>
                    <mark className="k-promotion__mark">{info.mark}</mark>
                    <h3 className="k-promotion__title" id={"pmTitle" + index}>{info.title}</h3>
                    <img src={require('../images/' + info.img)} alt="" className="k-promotion__img"/>
                    <p className="k-promotion__txt" id={"pmDescription" + index}>{info.txt}</p>
                    <a href={process.env.PUBLIC_URL + '/' + info.link} className="k-promotion__link" aria-labelledby={"pmTitle" + index} aria-describedby={"pmDescription" + index}>자세히 보기</a>
                </li>
        ));
        return (
            <section className="k-promotion__wrap k-section-full">
                <nav className="k-section k-promotion" aria-labelledby="promotion-title">
                    <h2 className="k-section__title" id="promotion-title">상품별 프로모션</h2>
                    <ul className="k-promotion__list">
                        {list}
                    </ul>
                </nav>
            </section>
        );
    }
}

export default Promotion;