import React, { Component } from 'react';
import $ from 'jquery';

class Reserv7 extends Component {
    render() {
        const { poster } = this.props;
        const { data } = this.props;
        return(
            <section className="reserv-step reserv-step__7">
                <div className="reserv-wrap">
                    <h2 className="reserv-step__title"><strong>예매가 완료</strong>되었습니다.</h2>
                    <div className="reserv-complete__wrap">
                        <h3 className="reserv-box__title">예매 내역</h3>
                        <ul className="reserv-info__list">
                            <li>
                                <em className="reserv-info__subject">예매번호</em>
                                <p className="reserv-info__txt">201911212000003-0569-49AC</p>
                            </li>
                            <li>
                                <em className="reserv-info__subject">공연명</em>
                                <p className="reserv-info__txt">{poster.pName}</p>
                            </li>
                            <li>
                                <em className="reserv-info__subject">관람일</em>
                                <p className="reserv-info__txt">{data.date}</p>
                            </li>
                            <li className="seat-li">
                                <em className="reserv-info__subject">좌석</em>
                                <p className="reserv-info__txt">
                                    <ul className="info-seat__list clearfix">
                                        {data.seatList.map((seat,index) => {
                                            return <li key={"seatList"+index}>{seat.seat}</li>
                                        })}
                                    </ul>
                                </p>
                            </li>
                            <li>
                                <em className="reserv-info__subject">결제금액</em>
                                <p className="reserv-info__txt">
                                    <strong>총 결제 {data.total} STAR</strong>
                                </p>
                            </li>
                        </ul>
                    </div>
                </div>
            </section>
        );
    }
}

export default Reserv7;