import React, { Component } from 'react';
import $ from 'jquery';
import { Link } from 'react-router-dom';

class SideMenu extends Component {
    componentDidMount() {
        const $input = $(".k-form__input");
        const $label = ".k-form__label";
        $input.focus(function() {
            $(this).siblings($label).addClass('active'); 
        });
        $input.blur(function() {
            let thisVal = $(this).val();
            if(!thisVal) {
                $(this).siblings($label).removeClass('active');
            }
        });
    }

    closeSideMenu = () => {
        $(".k-sidemenu__wrap").fadeOut(300);
        $(".k-sidemenu").removeClass("active");
    }

    handleSideCheck = (e) => {
        if(e.which === 13) {
            e.target.click();
            e.target.focus();
            e.target.checked ? e.target.setAttribute("aria-checked",true) : e.target.setAttribute("aria-checked",false);
        }        
    }
    handleCheckLabel = (e) => {
        const thisFor = e.target.getAttribute("for");
        const $check = $("input#" + thisFor);
        $check.is(":checked") ? $check.attr("aria-checked",false) : $check.attr("aria-checked",true);
    }

    trapTab = (field,e) => {
        if(field === "last") {
            if(e.keyCode === 9) {
                if(!e.shiftKey) {
                    $("#closeSide").focus();
                    e.preventDefault();
                }
            }
        }
        else if(field === "first") {
            if(e.keyCode === 9) {
                if(e.shiftKey) {
                    $(".k-sidemenu__nav > a").last().focus();
                    e.preventDefault();
                }
            }
        }
    }

    render() {
        return (
                <section className="k-sidemenu__wrap">
                    <article className="k-dimmer" onClick={this.closeSideMenu}></article>
                    <article className="k-sidemenu" id="sidemenu">
                        <form className="k-form k-sidemenu__form clearfix">
                            <button type="button" id="closeSide" aria-controls="sidemenu" className="k-sidemenu__close txt-hidden" onClick={this.closeSideMenu} onKeyDown={this.trapTab.bind(this,"first")}>
                            사이드메뉴 닫기</button>
                            <div className="form-input__box">
                                <input id="sideID" type="text" name="ID" className="k-form__input"/>
                                <label htmlFor="sideID" className="k-form__label">아이디(이메일)</label>
                            </div>
                            <div className="form-input__box">
                                <input id="sidePW" type="password" name="PW" className="k-form__input"/>
                                <label htmlFor="sidePW" className="k-form__label">비밀번호</label>
                            </div>
                            <button className="k-form__btn k-btn full">로그인</button>
                            <input id="idSave" type="checkbox" className="k-check k-hidden" aria-checked="false" onKeyPress={this.handleSideCheck}/>
                            <label htmlFor="idSave" className="k-check__label" onClick={this.handleCheckLabel}>아이디 저장</label>
                            <nav className="k-client__nav" aria-label="회원 관련 메뉴">
                                <a href="javascript:void(0)">회원가입</a>
                                <a href="javascript:void(0)">ID/PW 찾기</a>
                            </nav>
                        </form>
                        <nav className="k-sidemenu__nav" aria-label="서비스 이용관련 메뉴">
                            <Link to="/notice">공지사항</Link>
                            <a href="#">스타충전</a>
                            <a href="#">스타월렛</a>
                            <a href="#">예매방법</a>
                            <a href="#">FAQ</a>
                            <a href="#" onKeyDown={this.trapTab.bind(this,"last")}>문의 게시판</a>
                        </nav>
                    </article>
                </section>
        );
    }
}

export default SideMenu;