import React, { Component } from 'react';

class SeatGrade extends Component {
    static defaultProps = {
        name : []
    }
    
    render() {
        const { data } = this.props;
        return(
            <ul className="seat-grade__list">
                {data.map(function(name,index){
                    const style = {
                        "backgroundColor" : name.color
                    }
                    return <li className="seat-grade__li" key={"seat"+index}>
                        <i className="seat-grade__color" style={style}></i> 
                        <span className="seat-grade__name">{name.name}</span> 
                        <em className="seat-grade__price">{name.price} STAR</em>
                    </li>;
                })}
            </ul>
        );
    }
}

export default SeatGrade;