import React, { Component } from 'react';

class GuideCharge extends Component {
    render() {
        return (
            <div className="master-filter__tab" role="tabpanel" aria-labelledby="tabStarCharge">
                <div className="child-filter__wrap chu">
                    <div className="child-filter" role="tablist">
                        <a href="javascipt:void(0)" className="child-filter__a active" role="tab" id="ChargeGiftCard">Gift Card</a>
                        <a href="javascipt:void(0)" className="child-filter__a" role="tab" id="ChargeKST">KST</a>
                        <a href="javascipt:void(0)" className="child-filter__a" role="tab" id="ChargeVA">가상계좌</a>
                    </div>
                </div>
                <br/>
                <div className="child-filter__tab active" role="tabpanel" aria-labelledby="ChargeGiftCard" tabIndex="0">
                    <img src={require('../images/guide-giftcard.png')} alt=""/>
                </div>
                <div className="child-filter__tab" role="tabpanel" aria-labelledby="ChargeKST" tabIndex="0">
                    <img src={require('../images/guide-kst.png')} alt=""/>
                </div>
                <div className="child-filter__tab" role="tabpanel" aria-labelledby="ChargeVA" tabIndex="0">
                    <img src={require('../images/guide-v.png')} alt=""/>
                </div>
                
            </div>
        )
    }
}

export default GuideCharge;