import React, { Component } from 'react';

class Video extends Component {
    static defaultProps = {
        data : []
    }
    render() {
        const { data } = this.props;
        const list = data.map((info,index) => (
            <li className="swiper-slide" key={"video"+index}>
                <a href={info.link} target="_blank" className="k-video__link">
                    <img src={info.thumb} alt="" className="k-video__img"/>
                    <p className="k-video__title">{info.title}</p>
                </a>
            </li>
        ));
        return(
            <section className="k-video__wrap k-section-full">
                <div className="k-section">
                    <h2 className="k-section__title" id="video-title">홍보영상</h2>
                    <nav className="k-video swiper-container" aria-labelledby="video-title">
                        <ul className="swiper-wrapper">
                            {list}
                        </ul>
                        <div className="swiper-button-prev k-video__arrow"></div>
                        <div className="swiper-button-next k-video__arrow"></div>
                    </nav>
                </div>
            </section>
        );
    }
}

export default Video;