import React, { Component } from 'react';
import $ from 'jquery';

class InquiryList extends Component {
    static defaultProps = {
        data : []
    }

    handleInquiry = (e) => {
        const target = e.currentTarget;
        let isActive = target.classList.contains("active");
        let $qna = $(target).find(".inquiry-qna");
        let qnaHeight;
        if(isActive) {
            qnaHeight = 62;
            target.classList.remove("active");
            target.setAttribute("aria-expanded",false);
        }
        else {
            qnaHeight = $qna.outerHeight() + 62;
            target.classList.add("active");
            target.setAttribute("aria-expanded",true);
        }
        target.style.height = qnaHeight + "px";
    }

    handleInquiryByKey = (e) => {
        if(e.which === 13) {
            e.currentTarget.click();
        }
    }

    changeToForm = () => {
        this.props.onCreate(true);
    }

    render() {
        const { data } = this.props;
        return(
            <section className="inquiry-list__wrap">
                <a href="javascript:void(0);" className="k-btn inquiry__btn" onClick={this.changeToForm} role="button">문의하기</a>
                <ul className="inquiry-list k-list k-list__hd clearfix">
                    <li className="list-num">번호</li>
                    <li className="list-state">상태</li>
                    <li className="list-category">분류</li>
                    <li className="list-subject">제목</li>
                    <li className="list-date">등록일</li>
                </ul>
                {data.map((info,index) => {
                    let qnaMark;
                    if(info.state) {
                        qnaMark = <mark className="list-state__mark active">답변완료</mark>;
                    }
                    else {
                        qnaMark = <mark className="list-state__mark">답변대기</mark>
                    }
                    return <ul tabIndex="0" className="k-list inquiry-list k-list__body clearfix" aria-expanded="false" key={"inquiry-list"+index} onClick={this.handleInquiry} onKeyPress={this.handleInquiryByKey}>
                                <li className="list-num">{info.num}</li>
                                <li className="list-state">{qnaMark}</li>
                                <li className="list-category">{info.category}</li>
                                <li className="list-subject">{info.title}</li>
                                <li className="list-date">{info.date}</li>
                                <li className="inquiry-qna">
                                    <div className="inquiry-q">{info.q}</div>
                                    <div className="inquiry-a">{info.a}</div>
                                </li>
                            </ul>
                })}
            </section>
        );
    }
}

export default InquiryList;