import React, { Component } from 'react';
import $ from 'jquery';
import { Link } from 'react-router-dom';

import ReservInfo from './dataSet/ReservInfo';
import Popup from './layouts/Popup';
import InquiryList from './dataSet/InquiryList';
import InquiryForm  from './layouts/InquiryForm';
import MyInfo from './layouts/MyInfo';

class Mypage extends Component {
    state = {
        info : {},
        isPopup : false,
        formActive : false
    }
    getReservInfo = (reservInfo) => {
        console.log(reservInfo);
        this.setState({
            info : reservInfo,
            isPopup : true
        },() => {
            $(".k-popup__wrap").addClass("active").fadeIn(300).css("display","flex");
            $(".popup-detail__list").focus();
        });
    }

    getFormProps = (bool) => {
        this.setState({
            formActive : bool
        });
    }

    getListProps = (bool) => {
        this.setState({
            formActive : bool
        });
    }

    componentDidMount() {
        this.props.onCreate("mypage");
    }

    render() {
        const { isPopup } = this.state;
        const { formActive } = this.state;
        let popup,inquiryForm;
        if(isPopup) popup = <Popup data={this.state.info}/>;

        if(formActive) {
            inquiryForm = <InquiryForm onCreate={this.getFormProps}/>;
        }
        else {
            inquiryForm = <InquiryList data={this.props.inquiry} onCreate={this.getListProps}/>;
        }

        return (
            <section className="k-mypage__wrap k-section">
                <h2 className="k-main__title">마이페이지
                    <small className="k-main__subTxt">(fotrise@naver.com)</small>
                </h2>
                <div className="k-mypage__link">
                    <Link to="/personal" className="k-sm__btn">개인정보 보기</Link>
                    <a href="javascript:void(0)" className="k-sm__btn" role="button">로그아웃</a>
                </div>

                <MyInfo personal={this.props.personal}/>
                <div className="master-filter__wrap">
                    <div className="master-filter filter-2 clearfix" role="tablist">
                        <a href="javascript:void(0);" role="tab" id="tabRecent" aria-selected="true" className="master-filter__a active">최근 예매/취소 내역</a>
                        <a href="javascript:void(0);" role="tab" id="tabInquiry" aria-selected="false" className="master-filter__a">1:1 문의</a>
                    </div>
                </div>

                {/* Mypage Reserv Info */}
                <div className="master-filter__tab active" role="tabpanel" aria-labelledby="tabRecent">
                    <div className="child-filter__wrap">
                        <div className="child-filter">
                            <a href="javascript:void(0);" className="child-filter__a active" role="button" aria-label="1개월 필터">1개월</a>
                            <a href="javascript:void(0);" className="child-filter__a" role="button" aria-label="3개월 필터">3개월</a>
                            <a href="javascript:void(0);" className="child-filter__a" role="button" aria-label="6개월 필터">6개월</a>
                        </div>
                    </div>
                    <div className="sub-filter__wrap clearfix">
                        <div className="sub-filter">
                            <a href="javascript:void(0);" className="sub-filter__a active" role="button" aria-label="최근예매순 필터">최근예매순</a>
                            <a href="javascript:void(0);" className="sub-filter__a" role="button" aria-label="공연임박순 필터">공연임박순</a>
                        </div>
                    </div>
                    <ReservInfo data={this.props.data} onCreate={this.getReservInfo}/>
                    <div className="k-pagination__wrap">
                        <ul className="k-pagination clearfix">
                            <li tabIndex="0" className="k-pagination__arrow prev-arrow"></li>
                            <li tabIndex="0" className="k-pagination__num active">1</li>
                            <li tabIndex="0" className="k-pagination__arrow next-arrow"></li>
                        </ul>
                    </div>
                </div>

                {/* Mypage 1:1 Inquiry */}
                <div className="master-filter__tab" role="tabpanel" aria-labelledby="tabInquiry">
                    {inquiryForm}
                </div>

                {/* Popup */}
                {popup}
            </section>
        );
    }
}

export default Mypage;