import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class FAQ extends Component {

    componentDidMount() {
        this.props.onCreate("FAQ");
    }

    render() {
        const style = {
            padding: "15px 0 50px 0",
            textAlign : "center",
        }
        return (
            <section className="k-faq__wrap k-section" role="tabpanel" aria-labelledby="tabFAQ" tabIndex="0">
                <p style={style}>준비중입니다</p>
            </section>
        );
    }
}

export default FAQ;