import React, { Component } from 'react';
import PosterInfo from './PosterInfo';
import $ from 'jquery';

class PosterList extends Component {
    static defaultProps = {
        data: []
    }
    sliderFocus = () => {
        $(".k-poster__wrap *").attr("tabindex","-1");

        $(".k-poster__wrap").on("keydown keyup",function(e){
            console.log(e.which);
            let posterName = '';
            if(e.type === 'keyup') {
                posterName = $(".k-poster__wrap .swiper-slide-active .poster-title").text();
            }
            $(this).attr("aria-label",posterName);
            if(e.type === 'keydown') $(".k-poster__alert").hide();
            if(e.which === 37) {
                $(".swiper-button-prev.k-poster__arrow").click();
            }
            if(e.which === 39) {
                $(".swiper-button-next.k-poster__arrow").click();
            }
            if(e.which === 13) {
                const activeLink = $(".swiper-slide-active").eq(0).find(".k-link__wrap").attr("href");
                window.location.href = activeLink;
            }
        });
    }
    render() {
        const { data } = this.props;
        const list = data.slice(0).reverse().map(info => (
            <PosterInfo key={info.id} info={info}/>
        ));
        return (
            <section className="k-poster__wrap" tabIndex="0" onFocus={this.sliderFocus}>
                <div className="k-poster__alert" role="alert" aria-live="assertive">
                    공연 포스터 슬라이드 영역입니다. 방향키로 포스터를 넘길 수 있고, Enter를 누르면 해당 공연의 상세페이지로 이동합니다.
                </div>
                <div className="swiper-container k-poster__slide k-section" aria-hidden="true">
                    <div className="swiper-wrapper">{list}</div>
                    <div className="swiper-button-prev k-poster__arrow"></div>
                    <div className="swiper-button-next k-poster__arrow"></div>
                 </div>
            </section>
        );
    }
}

export default PosterList;